<template>
  <div class="form-con">
    <h2 class="title">
      {{$route.name === '修改广告素材' ? '修改广告素材' : '创建广告素材'}}
    </h2>
    <Form :model="form" class="form-group" label-position="right" :label-width="100">
      <FormItem label="图片地址" prop="url">
        <Input class="inputtxt" size="large" type="text" v-model.trim="url" placeholder="请输入图片地址" />
      </FormItem>
      <FormItem label="标识" prop="sign">
        <Input class="inputtxt" size="large" type="text" v-model.trim="sign" placeholder="请输入标识" />
      </FormItem>
      <FormItem label="权重" prop="weight">
        <Input class="inputtxt" size="large" type="text" v-model.trim="weight" placeholder="请输入权重" />
        <Button v-if="isUpdate" style="margin-left: 30px;" type="success" @click="saveUpdate">保存</Button>
        <Button v-else style="margin-left: 30px;" type="success" @click="add">添加</Button>
      </FormItem>
      <Table style="margin-bottom: 30px;" :columns="imgHeader" :data="form.imgList" />
      <FormItem label="标题" prop="title">
        <Input class="inputtxt" size="large" type="text" v-model.trim="form.title" placeholder="请输入素材标题"></Input>
      </FormItem>
      <FormItem label="小说链接" prop="novelLink">
        <Input class="inputtxt" size="large" type="text" v-model.trim="form.novelLink" placeholder="请输入小说链接"></Input>
      </FormItem>
      <FormItem>
        <Button v-if="$route.name === '创建广告素材'" class="edit-btn" size="large" type="primary" @click="create">立即创建
        </Button>
        <Button v-else class="edit-btn" size="large" type="primary" @click="save">保 存</Button>
        <Button class="edit-btn" size="large" type="warning" @click="cancel">取 消</Button>
      </FormItem>
    </Form>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        form: {
          imgList: [],
          title: "",
          novelLink: ""
        },
        url: '',
        weight: '',
        sign: '',
        isUpdate: false,
        index: null,
        imgHeader: [
          {
            key: 'url',
            title: '图片地址',
            align: 'center'
          }, {
            key: 'sign',
            title: '标识',
            align: 'center'
          }, {
            key: 'weight',
            title: '权重',
            align: 'center'
          }, {
            title: 'Action',
            align: 'center',
            render: (h, params) => {
              let that = this;
              return h('div', [
                h('Button', {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.url = params.row.url;
                      that.sign = params.row.sign;
                      that.weight = params.row.weight;
                      that.isUpdate = true;
                      that.index = params.index;
                    }
                  }
                }, '修改'),
                h('Button', {
                  props: {
                    type: "error",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.$Modal.confirm({
                        title: '提示:',
                        content: '确认删除这条内容吗？',
                        onOk: () => {
                          that.form.imgList.splice(params.index, 1);
                        }
                      })
                    }
                  }
                }, '删除'),
              ])
            }
          }
        ]
      }
    },
    mounted() {
      if(this.$route.name === '修改广告素材') {
        this.form = this.$route.params.row
      }
    },
    methods: {
      add() {
        if (this.url === "" || this.weight === "" || this.sign === "") {
          this.$Message.info("输入有误，请检查");
        } else {
          this.form.imgList.push({
            url: this.url,
            weight: this.weight,
            sign: this.sign
          });
          this.url = this.weight = this.sign = "";
        }
      },
      saveUpdate() {
        let message = {
          url: this.url,
          weight: this.weight,
          sign: this.sign
        };
        this.form.imgList.splice(this.index, 1, message)
        this.url = this.weight = this.sign = "";
        this.isUpdate = false
      },
      deleteLink(index) {
        this.$Modal.confirm({
          title: '提示:',
          content: '确认删除这条内容吗？',
          onOk: () => {
            this.form.links.splice(index, 1);
          }
        })
      },
      save() {
        this.$axios.put('/adMaterial', this.form)
          .then(res => {
            if (res.data.code === 1) {
              this.$Message.info(res.data.msg);
              this.$router.push({
                name: '广告素材',
                params: {
                  row: this.form,
                  index: this.$route.params.index
                }
              });
              this.reset()
            } else {
              this.$Message.info(res.data.msg);
            }
          });
      },
      create() {
        this.$axios.post('/adMaterial', this.form)
          .then(res => {
            if (res.data.code === 1) {
              this.$Message.info(res.data.msg);
              this.$router.push({
                name: '广告素材',
                params: res.data.data
              });
              this.reset()
            } else {
              this.$Message.info(res.data.msg);
            }
          });
      },
      cancel() {
        this.$router.push({
          name: '广告素材',
          params: {state: 'cancel'}
        });
        this.reset()
      },
      reset() {
        this.form = {
          imgList: [],
          title: "",
          novelLink: ""
        };
        this.url = this.weight = this.sign = "";
      }
    }
  }
</script>
<style scoped>
  .form-con {
    width: 900px;
    padding-top: 20px;
    margin: 0 auto;
    text-align: left;
  }

  .title {
    font-size: 36px;
    text-align: center;
    margin-bottom: 30px;
  }

  .inputtxt {
    width: 400px;
  }

  ul {
    list-style: none;
    overflow: hidden;
  }

  li {
    float: left;
    margin: 0 20px 20px 0;
  }

  li span {
    display: inline-block;
    padding: 0 15px;
    border: 1px solid #eee;
    border-radius: 5px;
    margin-right: 10px;
    cursor: pointer;
    text-align: center;
  }
  .weight span {
    width: 100px;
  }

  .input-box {
    width: 150px;
    height: 34px;
  }

  .edit-btn {
    margin-right: 20px;
  }
</style>
